.question {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

.current-quiz {
    width: 95%;
    min-height: 350px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    border: 5px solid grey;
    padding: 20px;
    margin-top: 10px;
}

.options {
    width: 100%;
    height: 4em;
    display: flex;
    flex-wrap: wrap;
    flex: 1;
    align-items: center;
    justify-content: space-around;
    margin: 10px;
}
.single-option {
    width: 45%;
    /* padding: 15px 20px; */
    padding: 0.8rem 1.5rem;
    margin: 10px;
    box-shadow: 0 0 2px black;
  }
  
  @media (max-width: 614px) {
    .singleOption {
      width: 100%;
    }
  }
  .select {
    background-color: rgb(7, 207, 0);
    color: white;
    box-shadow: 0 0 1px black;
  }
  
  .wrong {
    background-color: rgb(233, 0, 0);
    color: white;
    box-shadow: 0 0 1px black;
  }
  .controls {
    display: flex;
    width: 100%;
    justify-content: space-around;
  }